
* {
    box-sizing: border-box;
}

body {


    margin:0;
    font-family: Poppins, sans-serif;


}

/* navbar */

nav{
    height:70px;
    display: flex;
    padding: 20px 36px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}



/* hero */
section { 
    padding:20px;
}


.hero{
    display: flex;
    flex-direction: column;
    /* padding: 96px 36px; */

}

.hero_image{

    padding-left:57px;
    width : 400px;
    /* align-self: center; */
}

.hero--title{
    font-size: 36px;
    margin-bottom: 16px;
}

.hero--text{
    max-width: 330px;
    margin-top: 0;

}

/* card */
.cards-list{
    display: flex;

    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;

}

.card{
    width:175px;
    font-size: 12px;
    flex: 0 0 auto;         
    display: flex;          
    flex-direction: column; 
    position: relative;
}

.card--image{

    width: 100%;
    border-radius: 9px;
    margin-bottom: 9px;

}

/* ADD */
.card--title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card--stats{
    display:flex;
    align-items: center;


}

.card--star{
    height:14px;
}

/* ADD */
.card--price {
    margin-top: auto;
}

.gray{
    color:#918E9B;
}

.bold{
    font-weight:bold;
}

.card--badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}

